<template>
    <div>
        <v-menu offset-y v-model="menuValue">
            <template v-slot:activator="{ on }">
                <v-btn
                    text
                    @click.stop="drawer = !drawer"
                    v-on="on"
                    :class="{ active: menuValue }"
                    ><v-icon xLarge>mdi-menu</v-icon></v-btn
                >
            </template>
        </v-menu>

        <v-navigation-drawer
            v-model="drawer"
            absolute
            temporary
            right
            app
            clipped
            color="secondary"
            height="16em"
        >
            <v-treeview 
         :items="items"
         v-model="tree"
         >
                <template slot="label" slot-scope="props">
                    <v-icon v-if="props.item.icon">{{
                        props.item.icon
                    }}</v-icon>
                    <router-link
                        class="navigatorLink"
                        :to="props.item.route"
                        v-if="props.item.route"
                        @click.native="setTitle(props.item.title)"
                    >
                        {{ $t(props.item.name) }}
                    </router-link>
                </template>
            </v-treeview>
<!-- 
             <v-treeview 
             v-model="tree"
             :items="items">
                <template v-slot:prepend="{ item }">
                    <v-icon v-if="item.icon">{{
                        item.icon
                    }}</v-icon>
                    <router-link
                        class="navigatorLink"
                        :to="item.route"
                        v-if="item.route"
                        @click.native="setTitle(item.name)"
                    >
                        {{ $t(item.name) }}
                    </router-link>
                </template>
            </v-treeview> -->
        </v-navigation-drawer>
    </div>
</template>

<script>
export default {
    data: () => ({
        menuValue: false,
        drawer: false,
        group: null,
        tree: [],
        items: [
            { name: "navigation.home", icon: "mdi-home", route: "/", title: "empty",},
            {
                name: "navigation.about",
                icon: "mdi-information-outline",
                title: "about",
                route: "/about",
                children: [
                    {
                        name: "navigation.hobbies",
                        route: "/hobby",
                        title: "hobbies",
                    },
                    {
                        name: "navigation.values",
                        route: "/values",
                        title: "values",
                    },
                    {
                        name: "navigation.travel",
                        route: "/travel",
                        title: "travel",
                    },
                    {
                        name: "navigation.offer",
                        route: "/offer",
                        title: "offer",
                    },
                ],
            },
            { name: "navigation.work", 
            icon: "mdi-hammer", 
            route: "/work",
            title: "work",
             },
            { name: "navigation.guestbook", 
            icon: "mdi-book", 
            route: "/guestbook",
            title: "guestbook",
             },
            {
                name: "navigation.contact",
                icon: "mdi-account-arrow-left",
                route: "/contact",
                title: "contact",
            },
        ],
    }),

    watch: {
        group() {
            this.drawer = false;
        },
    },

    methods: {
        setTitle(title) {
            console.log("asdf" +title)
            this.$store.dispatch("setTitle", title);
        },
    },
};
</script>

<style scoped>
.v-btn.active .v-icon {
    transform: rotate(-360deg);
}
.navigatorLink {
    margin-top: 0.1em;
    color: white;
    text-decoration: none;
}
</style>
