<template>
    <v-form ref="form" class="guestbookform" v-model="valid" lazy-validation>
        <v-text-field
            v-model="name"
            :counter="30"
            :rules="[
                (v) => !!v || $t('contact.name_required'),
                (v) => (v && v.length <= 30) || $t('contact.name_less30'),
            ]"
            label="Name"
            required
        ></v-text-field>
        <h4>{{ $t("did you enjoy my page?") }}</h4>
        <span
            ><v-rating
                v-model="rating"
                background-color="highlight lighten-3"
                color="highlight"
                large
            ></v-rating
        ></span>
        <v-textarea
            outlined
            v-model="message"
            :rules="[
                (v) => !!v || $t('contact.message_required'),
                (v) => (v && v.length >= 5) || $t('contact.message_more5'),
                (v) => (v && v.length <= 160) || $t('message_less160'),
            ]"
            name="input-7-4"
            :label="$t('contact.message')"
            value=""
        >
        </v-textarea>

        <v-row>
            <v-spacer></v-spacer>
            <v-btn
                :disabled="!isValidated"
                color="secondary"
                class="mr-4 mb-5"
                @click="validate"
                text
            >
                <h2 class="font-italic">{{ $t("submit") }}</h2>
            </v-btn>
        </v-row>
    </v-form>
</template>

<script>
import firebase from "firebase/app";

export default {
    data: () => ({
        valid: false,
        name: "",
        rating: 3,
        message: "",
        select: null,
    }),

    methods: {
        validate() {
            if (this.$refs.form.validate()) {
                console.log("add guestbook entry");
                console.log(firebase.firestore.Timestamp.fromDate(new Date()));
                this.$store.dispatch("addGuestbookEntry", {
                    name: this.name,
                    date: firebase.firestore.Timestamp.fromDate(new Date()),
                    rating: this.rating,
                    message: this.message,
                });
            }
        },
        reset() {
            this.$refs.form.reset();
        },

        mounted() {
            this.valid = this.$refs.form.validate();
        },
    },
    computed: {
        isValidated() {
            console.log("sync" + this.valid);

            return this.valid;
        },
    },
};
</script>
<style scoped>

</style>
